.toy-main-top {
   display: flex;
   justify-content: space-between;
   border-bottom: 2px solid $clr8;
   padding: $layoutPadding;
   padding-bottom: 10px;

   .toy-filter-form {
      display: flex;
      justify-content: center;
      align-items: center;
      align-content: center;
      gap: 10px;
      margin-top: 10px;
   }

   .add-toy-btn,
   .paging-container {
      @include flexCenter;
      margin-top: 10px;
      gap: 10px;
   }
}