.app-header {
   display: flex;
   justify-content: space-between;
   align-items: center;
   align-content: center;
   padding-right: $layoutPadding;
   padding-left: $layoutPadding;
   border-bottom: 1px solid $clr5;
   box-shadow: 0 0 2px 0px;
   height: 70px;

   nav a {
      margin: 10px;
      padding: 10px;
      font-size: rem(19px);

      &:hover {
         background-color: $clr1;
         border-radius: 5px;
         color: $clr2;
      }
   }

   nav a:hover {
      color: $clr6;
   }

   .right-header {
      display: flex;
      gap: 20px;
      cursor: pointer;
      font-size: rem(22px);
      padding-top: 7px;
   }

   .cart {
      position: fixed;
      top: 75px;
      right: 101px;
      background-color: white;
      width: 200px;
      height: 200px;
      border-radius: 10px;
      cursor: unset;
      box-shadow: 0 0 2px 2px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      padding: 10px;
      font-size: rem(13px);

      hr {
         width: 100%;
      }
   }

}